<template>
  <div>
    <el-row style="margin-top: 10%;">
      <transition name="el-fade-in-linear">
        <span v-show="show1">
          <el-col><span class="title">{{msg}}</span></el-col>
        </span>
      </transition>
    </el-row>
    <el-row style="margin-top: 2%;">
      <transition name="el-fade-in-linear">
        <div v-if="show2" class="sculpter-container">
          <img src="../assets/images/wall.jpg" alt="sculpter" class="sculpter">
        </div>
      </transition>
    </el-row>
    <el-row style="margin-top: 2%;">
      <transition name="el-fade-in-linear">
        <div v-if="show3" class="sculpter-container">
          <el-button style="width: 150px;" type="primary" @click="goBlog()">Start !</el-button>
        </div>
      </transition>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'Home',
  mounted () {
    var t = this
    var showTitleTimer1 = setTimeout(showTitle1, 500)
    function showTitle1 () {
      console.log('show1')
      t.show1 = true
      clearTimeout(showTitleTimer1) // 删除定时器 释放内存
    }
    var showTitleTimer2 = setTimeout(showTitle2, 560)
    function showTitle2 () {
      console.log('show2')
      t.show2 = true
      clearTimeout(showTitleTimer2) // 删除定时器 释放内存
    }
    var showTitleTimer3 = setTimeout(showTitle3, 620)
    function showTitle3 () {
      console.log('show3')
      t.show3 = true
      clearTimeout(showTitleTimer3) // 删除定时器 释放内存
    }
  },
  data () {
    return {
      msg: 'Welcome to My World !',
      show1: false,
      show2: false,
      show3: false
    }
  },
  methods: {
    goBlog () {
      // console.log('goBlog()')
      this.$router.push('Blog')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import '../assets/css/home.css';
</style>



